import React, { FC } from 'react'
import styles from './_forms.module.scss'
import cn from 'classnames'
import { SocialMedia } from '../SocialMedia/SocialMedia'
import { show } from '../../base/customTypes'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { solid } from '@fortawesome/fontawesome-svg-core/import.macro'

interface showLightboxProps{
  showLightbox: show
  click: any
}

const LoginForm:FC<showLightboxProps> = (props) => {

  const handleSubmit = (e: React.MouseEvent<HTMLInputElement>) => {
    e.preventDefault()
  }

  return (
    <div className={cn(styles['lightbox'], [styles[`lightbox--${props.showLightbox}`]])}>
    <section className={styles['form-container']}>
    <FontAwesomeIcon icon={solid('xmark')} size='lg' 
    className={styles['close-login']}
    onClick={props.click} />
      
    <form className={styles['form']}>
      <h4 className={styles['form__title']}>Inicia sesin o Registrate para acceder</h4>
    <div className={styles['social-media-container']}>
      <SocialMedia size='2x'/>
    </div>

      <span>- O -</span>

    <div className={styles['form__field-container']}>
        <div className={styles['form__field']}>
            <label htmlFor="form-mail">Introduzca su correo electrnico</label>
            <input type="email" name='form-mail' placeholder='Correo electrnico'/>
          </div>

          <div className={styles['form__field']}>
            <label htmlFor="form-password">Introduzca su contrasea</label>
            <input type="password" name='form-password'  placeholder='Contrasea'/>
          </div>

      <input type="submit" value="Iniciar sesin" className={cn(styles['form__field'], [styles['form__field--submit']])} onClick={handleSubmit}/>
    </div>


      <div className={styles['form__cta-container']}>
        <a href="https://google.com" className={styles['form__cta']}>Crear Cuenta</a>
        <a href="https://google.com" className={styles['form__cta']}>Ha olvidado su contrasea?</a>
      </div>

    </form> 

    </section>
    
    </div>
  )
}

export default LoginForm